@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap');

main {
    width: 100;
    padding-left: 40px;
    .top {
        margin-top: 10px;
        margin-bottom: 10px;
    }

    .projects {
        width: 100;
        height:  100vh;
        display: grid;
        grid-template-columns: repeat(auto-fill, 200px);
        grid-auto-rows: 200px;
        grid-gap: 10px;

        a {
            text-decoration: none;
            div {
                height: 200px;
                width: 200px;
                margin-bottom: 10px;
                background-size: 200px;
                border-radius: 10px;
                transition: 0.3s;
                display: flex;
                justify-content: center;
                align-items: center;
                flex-direction: column;

                img {
                    height: 200px;
                    width: 200px;
                    transition: 0.3s;
                }

                p {
                    position: relative;
                    visibility: hidden;
                    transition: 0.3s;
                }

                .circle {
                    display: none;
                    visibility: hidden;
                    background: #08fdd8;
                    height: 100px;
                    width: 100px;
                    border-radius: 50%;

                    p {
                        color: black;
                        font-family: 'Montserrat';
                    }
                }
            }

            div:hover {
                img {
                    opacity: 0.3;
                    position: absolute;
                }

                p {
                    visibility: visible;
                }

                .circle {
                    display: flex;
                    visibility: visible;
                }
            }
            
            .browser-vsc {
                background-color: #1f5d86b7;
            }
    
            .page-transition {
                background-color: #5c8700;
            }
        
            .arduino-bt {
                background-color: #09494d;
            }
        }
    }
}

@media screen and (max-width: 600px) {
    main {
        padding-left: 20px;
    }
}